.popup_overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba($color: #000000, $alpha: 0.2);
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  overflow-y: auto;

  .popup {
    border-radius: 10px;
    padding: 20px 15px;
    max-width: 550px;
    width: 100%;
    height: 100%;
    animation: anim_in 0.3s cubic-bezier(0.19, 1, 0.22, 1);
    .close {
      position: absolute;
      top: 15px;
      right: 15px;
      cursor: pointer;
      animation: hide_anim 0.5s;
      svg {
        height: 20px;
        fill: var(--white);
        transition: all 0.2s;
        &:hover {
          fill: var(--error);
        }
      }
    }
    .footer {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      gap: 20px;
    }
  }
}
@keyframes anim_in {
  from {
    transform: scale(0.2);
  }
  to {
    transform: scale(1);
  }
}

@keyframes hide_anim {
  0% {
    opacity: 0;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
