.wrapper {
  div {
    line-height: 25px;
  }
  .title_switch_wrap {
    text-transform: capitalize;
    text-align: center;
    margin-bottom: 20px;
    background-color: var(--white);
    color: var(--primary-color);
    // padding: 20px;
    border-radius: 20px;
    box-shadow: rgba(10, 53, 83, 0.45) 0px 25px 20px -20px;
    display: flex;
    justify-content: center;
    .title {
      padding: 20px;
      flex-grow: 1;
      flex-basis: 0;
    }
    .switch {
      flex-grow: 1;
      flex-basis: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--primary-color);
      box-shadow: rgba(10, 53, 83, 0.45) 0px 25px 20px -20px;
      border-left: var(--border);
      padding: 20px;
      div {
        font-weight: bold;
        font-size: 19px;
        padding-left: 10px;
        cursor: pointer;
        &.active {
          cursor: default;
          color: var(--active-color);
        }
        &:first-child {
          border-right: 2px solid var(--primary-color);
          border-radius: 0;
          padding-left: 0;
          padding-right: 10px;
        }
      }
    }
  }
  .chart {
    padding: 20px;
    background: var(--white);
    border-radius: 20px;
    color: var(--primary-color);
    margin-bottom: 20px;
    box-shadow: rgba(10, 53, 83, 0.45) 0px 25px 20px -20px;
  }

  .box_wrap {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
    div {
      padding: 20px;
      background: var(--white);
      color: var(--primary-color);
      border-radius: 20px;
      width: 200px;
      text-align: center;
      box-shadow: rgba(10, 53, 83, 0.45) 0px 25px 20px -20px;
    }
  }
  .blank_elem {
    height: 5px;
  }
}
