.btn_wrap {
  button {
    padding: 6px 13px;
    color: var(--white);
    border-radius: var(--border-radius);
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    svg {
      width: 20px;
      fill: #fff;
      display: flex;
    }
    &.right {
      flex-direction: row-reverse;
      svg {
        margin-left: 10px;
      }
    }
    &.left {
      flex-direction: row;
      margin-right: 10px;
    }
  }
  &.primary {
    button {
      background: transparent;
      color: var(--primary-color);
      border: var(--border);
      &:hover {
        background: var(--primary-color);
        color: var(--white);
        border: var(--border);
      }
    }
  }
  &.primary_bg {
    button {
      background: var(--primary-color);
      color: var(--white);
      border: var(--border);
      &:hover {
        background: var(--primary-hover-color);
      }
    }
  }
  &.danger {
    button {
      background: transparent;
      color: var(--error);
      border: var(--error-border);
      &:hover {
        background: var(--error);
        color: var(--white);
      }
    }
  }
}
