.weather_wrap {
  padding: 20px;
  .search_wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;
  }
  .city_name {
    margin: 50px auto;
    font-size: 22px;
    font-weight: bold;
    text-align: center;
    color: var(--primary-color);
    word-break: break-word;
    line-height: 30px;
  }
  .skeleton_wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
    span {
      border-radius: 10px;
    }
  }
}
